<template>
    <v-container grid-list-lg id='checkout' align='center' style='margin-bottom:25px;margin-top:15px;'>
        <v-layout row wrap>
            <v-flex xs12 sm12 md12>

                <!-- Checkout Page -->
                <v-card v-show='checkoutPage' elevation="4" color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                        Каса
                        </v-app-bar>
                    </v-card-title>

                    <v-layout v-show='loadingData' row align-center justify-center>
                        <v-flex xs10 sm10 md6>
                        <v-card
                        elevation="2"
                        loading
                        class='primary--loading text-md-h6 text-sm-subtitle-1 font-weight-black primary--text text-xs-center'
                        height='50'
                        align='center'
                        justify='center'
                        >
                        <p style='margin-top:10px;'>Потвърждаване на плащането...</p>
                        </v-card>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='parcel_exists && shipment_type==0' row align-center justify-center>
                        <v-flex xs3 sm2 md2>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Номер на пратка"
                            :value='parcel_data.orderNumber'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md3>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Вид пратка:"
                            :value='parcel_type'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm3 md3>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Тип доставка:"
                            :value='delivery_type'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md2>
                        <v-text-field
                            readonly
                            :value='parcel_data.paymentAmount_BGN'
                            filled
                            background-color='white darken-3'
                            suffix='лв.'
                            label="Цена:"
                            class="text-md-h6 centered-input"
                            color="primary" >
                        </v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout v-if='parcel_exists && shipment_type==0' row align-center justify-center>
                        <v-flex xs3 sm3 md3>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Подател:"
                            :value='parcel_data.senderDetails.senderName'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md2>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Телефон подател:"
                            :value='parcel_data.senderDetails.senderPhone'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md3>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Получател:"
                            :value='parcel_data.recipientDetails.recipientName'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md2>
                        <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Телефон получател:"
                            :value='parcel_data.recipientDetails.recipientPhone'
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='parcel_exists && shipment_type==1' row align-center justify-center>
                        <v-flex xs3 sm2 md2>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Номер на пратка"
                            :value='parcel_data.orderNumber'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md4>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Вид услуга:"
                            :value='service_type'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm3 md2>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Приоритет:"
                            :value='priority_type'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md2>
                        <v-text-field
                            readonly
                            :value='parcel_data.paymentAmount_BGN'
                            filled
                            background-color='white darken-3'
                            suffix='лв.'
                            label="Цена:"
                            class="text-md-h6 centered-input"
                            color="primary" >
                        </v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout v-if='parcel_exists && shipment_type==1' row align-center justify-center>
                        <v-flex xs3 sm3 md3>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Подател:"
                            :value='parcel_data.senderDetails.senderName'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md2>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Телефон подател:"
                            :value='parcel_data.senderDetails.senderPhone'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md3>
                            <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Получател:"
                            :value='parcel_data.recipientDetails.recipientName'
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm2 md2>
                        <v-text-field
                            readonly
                            background-color='white darken-3'
                            filled
                            label="Телефон получател:"
                            :value='parcel_data.recipientDetails.recipientPhone'
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-slide-y-transition>
                    <v-layout v-if='show_payment_message' row wrap justify-center>
                        <v-flex xs10 sm10 md10>
                                <v-alert
                                :type="payment_message_type">
                                {{payment_message}}
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-slide-y-transition>

                    <v-layout v-if='!loadingData && payment_successful && !notCompleted && shipment_type==0' row justify-center>
                        <v-flex xs8 sm8 md4>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            style='margin-bottom:10px;'
                            target="_blank"
                            link to="/send"
                        >
                            СЪЗДАЙ НОВА ПРАТКА</v-btn
                        >
                        </v-flex>
                        <v-flex xs8 sm8 md4>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            style='margin-bottom:10px;'
                            @click='orderParcel'
                        >
                            РАЗПЕЧАТАЙ ТОВАРИТЕЛНИЦАТА</v-btn
                        >
                        </v-flex>
                    </v-layout>
                    <v-layout v-if='!loadingData && !payment_successful && !notCompleted' row justify-center>
                        <form
                            target='_blank' 
                            action="https://www.transcard.bg/pay-gw/vpos"
                            method="POST"
                            >
                            <input type="hidden" name="PAGE" value="paylogin">
                            <input type="hidden" name="ENCODED" :value="tcf_data.payload.ENCODED">
                            <input type="hidden" name="CHECKSUM" :value="tcf_data.payload.CHECKSUM">
                            <input type="hidden" name="URL_OK" :value="transcard_url_ok">
                            <input type="hidden" name="URL_CANCEL" :value="transcard_url_cancel">
                            <input type="hidden" name="LANG" value="bg">
                        <v-flex xs8 sm8 md4>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            style='margin-bottom:10px;'
                            type="submit"
                            @click="attemptPayment()"
                        >
                            Плати</v-btn
                        >
                        </v-flex>
                        </form>
                    </v-layout>

                    <v-layout v-if='notCompleted' row justify-center>
                        <v-flex xs8 sm8 md4>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            style='margin-bottom:10px;'
                            link to='/'
                        >
                            Назад към сайта</v-btn
                        >
                        </v-flex>
                    </v-layout>

                    <v-card-text class='text-md-h6 font-weight-bold' v-if="!loadingData && payment_successful && !notCompleted && shipment_type==1" align='center'>
                        Благодарим за поръчката! До 2 работни часа ще получите товарителницата на посочената ел. поща!
                    </v-card-text>
                    
                    <v-layout v-if="!loadingData && payment_successful && !notCompleted && shipment_type==1"  row justify-center>
                    <!-- <v-flex xs8 sm8 md4>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            style='margin-bottom:10px;'
                            target="_blank"
                            link to="/send"
                        >
                            СЪЗДАЙ НОВА ПРАТКА</v-btn
                        >
                    </v-flex> -->
                    <v-flex xs8 sm8 md4>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            style='margin-bottom:10px;'
                            link to='/send'
                        >
                            Подготви друга пратка</v-btn
                        >
                        </v-flex>
                    </v-layout>

                </v-card>

                <!-- Amend Parcel Details Page -->
                <v-card v-show='editDetailsPage' elevation="4" color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                        {{parcel_data.orderNumber}} - Грешна информация
                        </v-app-bar>
                    </v-card-title>
                    <!-- Details Domestic -->
                    <v-form
                        v-show='shipment_type == 0'
                        ref="form_finish"
                        @submit.prevent=""
                        @submit="amendParcelData"
                        v-model="valid_form_finish"
                        lazy-validation
                        autocomplete='off'
                    >

                        <!-- Error Message -->
                        <v-expand-x-transition ease-in>
                            <v-layout v-show='evropat_error' row wrap justify-center>
                                <v-flex xs8 sm10 md10>
                                        <v-alert
                                        type="error"
                                        dismissible
                                        > {{ evropat_error_message }}
                                        </v-alert>
                                </v-flex>
                            </v-layout>
                        </v-expand-x-transition>
                        
                        <!-- Sender -->
                        <v-layout row wrap justify-left>
                            <v-flex xs10 sm10 md12>
                            <span><h3>Подател</h3></span>
                            </v-flex>
                        </v-layout>
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md10>
                                <v-text-field
                                v-model="parcel_data.parcelDetails['shipmentDescription']"
                                background-color='white darken-3'
                                required
                                filled
                                label="Описание на пратката"
                                hint='Кратко описание на съдържанието'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md3>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderName']"
                                background-color='white darken-3'
                                required
                                filled
                                label="Име"
                                hint='Имена на подателя'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderPhone']"
                                type='number'
                                background-color='white darken-3'
                                required
                                filled
                                label="Телефон"
                                hint='Ако номерът не започва с (+359) напишете целия телефонен номер'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md3>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderEmail']"
                                background-color='white darken-3'
                                required
                                filled
                                label="Ел. Поща"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderOrganization']"
                                background-color='white darken-3'
                                filled
                                label="Фирма"
                                hint='В случай на физическо лице оставете празно'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <v-layout v-if='addressOfficeService || addressService' row wrap justify-center>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderProvince']"
                                background-color='white darken-3'
                                readonly
                                filled
                                label="Област на подателя"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderCity']"
                                background-color='white darken-3'
                                readonly
                                filled
                                label="Град на подателя"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md4>
                                <v-combobox
                                v-model="parcel_data.senderDetails['senderAddress']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="Адрес"
                                hint='В случай, че адресът не е вече в нашата база, ще го запаметим.'
                                persistent-hint
                                ></v-combobox>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderAddressNumber']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="№"
                                hint='№ на ул./бул.'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderBuildingNumber']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="Бл."
                                hint='№ на блок'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <v-layout v-if='addressOfficeService || addressService' row wrap justify-center>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderEntranceNumber']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="Вх."
                                hint='№ на вход'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderApartmentNumber']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="Ап."
                                hint='№ на aп.'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderFloorNumber']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="Ет."
                                hint='№ на етаж'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md7>
                                <v-text-field
                                v-model="parcel_data.senderDetails['senderAddressAddition']"
                                readonly
                                background-color='white darken-3'
                                filled
                                label="Бележка"
                                hint='Допълнителна информация за адреса'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <!-- Recipient -->
                        <v-layout row wrap justify-left>
                            <v-flex xs10 sm10 md12>
                            <span><h3>Получател</h3></span>
                            </v-flex>
                        </v-layout>
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md3>
                                <v-text-field
                                v-model="parcel_data.recipientDetails['recipientName']"
                                required
                                :rules="parcelValidationNameRules"
                                background-color='white darken-3'
                                filled
                                label="Име"
                                hint='Имена на получателя'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.recipientDetails['recipientPhone']"
                                required
                                :rules="parcelValidationPhoneRules"
                                background-color='white darken-3'
                                filled
                                label="Телефон"
                                hint='Ако номерът не започва с (+359) напишете целия телефонен номер'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md3>
                                <v-text-field
                                v-model="parcel_data.recipientDetails['recipientEmail']"
                                background-color='white darken-3'
                                filled
                                label="Ел. Поща"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.recipientDetails['recipientOrganization']"
                                background-color='white darken-3'
                                filled
                                label="Фирма"
                                hint='В случай на физическо лице оставете празно'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <v-layout v-if='officeAddressService || addressService' row wrap justify-center>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.recipientDetails['recipientProvince']"
                                background-color='white darken-3'
                                readonly
                                filled
                                label="Област"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                v-model="parcel_data.recipientDetails['recipientCity']"
                                background-color='white darken-3'
                                readonly
                                filled
                                label="Населено място"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md4>
                                <v-combobox
                                readonly
                                v-model="parcel_data.recipientDetails['recipientAddress']"
                                background-color='white darken-3'
                                filled
                                label="Адрес"
                                hint='В случай, че адресът не е вече в нашата база, ще го запаметим.'
                                persistent-hint
                                ></v-combobox>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                readonly
                                v-model="parcel_data.recipientDetails['recipientAddressNumber']"
                                background-color='white darken-3'
                                filled
                                label="№"
                                hint='№ на ул./бул.'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                readonly
                                v-model="parcel_data.recipientDetails['recipientBuildingNumber']"
                                background-color='white darken-3'
                                filled
                                label="Бл."
                                hint='№ на блок'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <v-layout v-if='officeAddressService || addressService' row wrap justify-center>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                readonly
                                v-model="parcel_data.recipientDetails['recipientEntranceNumber']"
                                background-color='white darken-3'
                                filled
                                label="Вх."
                                hint='№ на вход'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                readonly
                                v-model="parcel_data.recipientDetails['recipientApartmentNumber']"
                                background-color='white darken-3'
                                filled
                                label="Ап."
                                hint='№ на aп.'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md1>
                                <v-text-field
                                readonly
                                v-model="parcel_data.recipientDetails['recipientFloorNumber']"
                                background-color='white darken-3'
                                filled
                                label="Ет."
                                hint='№ на етаж'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md7>
                                <v-text-field
                                readonly
                                v-model="parcel_data.recipientDetails['recipientAddressAddition']"
                                background-color='white darken-3'
                                filled
                                label="Бележка"
                                hint='Допълнителна информация за адреса'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <v-layout row wrap justify-center>
                            <v-flex xs8 sm10 md3>
                            <v-btn
                                block
                                rounded
                                color="primary"
                                type="submit"
                            >
                                Коригирай информация</v-btn
                            >
                        </v-flex>
                        </v-layout>
                    </v-form>

                    <!-- Details International -->
                    <v-form
                        v-show='shipment_type == 1'
                        ref="international_form_finish"
                        @submit.prevent=""
                        @submit="amendParcelData"
                        v-model="international_valid_form_finish"
                        lazy-validation
                        autocomplete='off'
                    >
                        <!-- Sender -->
                        <v-layout row wrap justify-left>
                            <v-flex xs10 sm10 md12>
                            <span><h3>Подател</h3></span>
                            </v-flex>
                        </v-layout>
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md8>
                                <v-text-field
                                v-model="international_shipmentDescription"
                                background-color='white darken-3'
                                required
                                :rules="international_parcelValidationDescriptionRules"
                                filled
                                label="Описание на пратката"
                                hint='Кратко описание на съдържанието'
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-combobox
                                required
                                clearable
                                v-model="international_tariff_code"
                                :rules="international_tariffCodeRules"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Тарифен код на пратката"
                                hint='Тарифния код трябва да отговаря на съдържанието на пратката'
                                persistent-hint
                                ></v-combobox>
                            </v-flex>
                        </v-layout>

                        <!-- Tariff Explained -->
                        <v-expand-x-transition ease-in>
                            <v-layout row wrap justify-center>
                                <v-flex xs8 sm10 md10>
                                    <v-alert
                                    type="warning"
                                    class='theme--light'
                                    >
