<script>
import axios from 'axios';

export default {
    name: 'ParcelCheckout',


    data: () => ({
        loading_data: true,
        notCompleted: false,
        payment_successful: false,
        show_payment_message: false,
        parcel_exists: false,
        payment_message: '',
        payment_message_type: '',
        payment_status: '',
        parcel_data: {},
        checkoutPage: true,
        editDetailsPage: false,
        waybillPage: false,

        loadingData: true,

        show_courier_message: false,
        courier_message: '',
        courier_message_type: '',
        transcard_payment_encoded: '',
        transcard_payment_checksum: '',

        // - Waybill
        selectedWaybill: '',
        databaseWaybills: [],
        database_data: {},
        instructionsCourier: false,
        instructionsCourier2: false,
        instructionsWaybill: false,
        pickupTimeFrom: '',
        pickupTimeTo: '',
        pickupTime: ['09:00', '09:15', '09:30', '09:45', '10:00', '10:15', '10:30', '10:45', '11:00', '11:15', '11:30', '11:45',
        '12:00', '12:15', '12:30', '12:45', '13:00', '13:15', '13:30', '13:45', '14:00', '14:15', '14:30', '14:45',
        '15:00', '15:15', '15:30', '15:45', '16:00', '16:15', '16:30', '16:45', '17:00', '17:15', '17:30', '17:45'],

        
    }),

    computed: {
        transcard_url_ok() {
            return 'https://pratkabg.com/checkout?parcel_invoice=' + this.parcel_invoice  + '&phone_number=' + this.phone_number  + '&shipment_type=' + this.shipment_type
        },

        transcard_url_cancel() {
            return 'https://pratkabg.com/checkout?parcel_invoice=' + this.parcel_invoice  + '&phone_number=' + this.phone_number  + '&shipment_type=' + this.shipment_type
        },
        remainingPickupTime() {
            let time = this.pickupTimeFrom
            let holderArray = this.pickupTime

            let indexTime = this.pickupTime.findIndex(object => {
                return object === time;
            });
            holderArray = holderArray.slice(indexTime+1)
            return holderArray
        },
        parcel_invoice() {
            return this.$route.query.parcel_invoice
        },
        phone_number() {
            return this.$route.query.phone_number
        },
        shipment_type() {
            return this.$route.query.shipment_type
        },
        delivery_type() {
            try {
                let holder = this.parcel_data.parcelDetails.deliveryType
                if(holder == 1){
                    return 'Офис до Oфис'
                }if(holder == 2){
                    return 'Офис до Aдрес'
                }if(holder == 3){
                    return 'Адрес до Oфис'
                }else{
                    return 'Адрес до Aдрес'
                }
            } catch(e){
                return false
            }
        },
        priority_type() {
            try {
                let holder = this.parcel_data.parcelDetails.priority
                if(holder == 'E'){
                    return 'Икономична'
                }if(holder == 'F'){
                    return 'Експресна'
                }
                else {
                    return ''
                }
            } catch(e){
                return false
            }
        },
        service_type() {
            try {
                let holder = this.parcel_data.parcelDetails.serviceType
                if (holder == 'LTR') {
                // console.log(this.international_weight <= 2)
                return 'Икономична, без проследяване'
                }
                else if (this.serviceType_internaltional == 'TRC') {
                    // console.log(this.international_weight <= 2)
                    return 'Икономична, с проследяване'
                }
                else if (this.serviceType_internaltional == 'PRM') {
                    // console.log(this.international_weight <= 20)
                    return 'Колетна'
                }
                else if (this.serviceType_internaltional == 'EXP') {
                    // console.log(this.international_weight <= 15)
                    return 'Експресна Колетна'
                }
                else {
                    return ''
                }
            } catch(e){
                return false
            }
        },
        parcel_type() {
            try {
            let holder = this.parcel_data.parcelType
            if(holder == 1){
                return 'Документи'
            }if(holder == 2){
                return 'Колетна пратка'
            }if(holder == 3){
                return 'Палет'
            }else{
                return 'Пратка'
            }
            } catch(e){
            return false
        }
        },
        addressOfficeService() {
            if(this.delivery_type=='Адрес до офис') {
                return true
            } else {
                return false
            }
        },

        addressService() {
            if(this.delivery_type=='Адрес до адрес') {
                return true
            } else {
                return false
            }
        },
    },

    methods: {
        async orderCourier() {
            let payload ={
                'pickupTimeFrom': this.pickupTimeFrom,
                'pickupTimeTo': this.pickupTimeTo,
                'shipmentBarcode': this.selectedWaybill,
                'destID': this.parcel_data.senderDetails.senderDestID,
                'officeID': '',
                'companyName': this.parcel_data.senderDetails.senderOrganization,
                'address': this.parcel_data.senderDetails.senderAddress,
                'addressNumber': this.parcel_data.senderDetails.senderAddressNumber,
                'personName': this.parcel_data.senderDetails.senderName,
                'phone': this.parcel_data.senderDetails.senderPhone,
                'phoneRegion': '',
            }
            let response = await axios.post('/parcel/evropat/create-courier-request', payload)
            // console.log(response)
            try {
                if(response.data.error == null){
                this.show_courier_message = true
                this.courier_message = 'Куриерът е успешно заявен.'
                this.courier_message_type = 'success'
                }
            }catch(e){
                this.show_courier_message = true
                this.courier_message = 'Куриерът е не е заявен.'
                this.courier_message_type = 'error'
            }
        },

        async shipmentPrintA4() {
            const payload = {
                'shipmentBarcode': this.selectedWaybill,
                'printoutType': 'A4'
            }
            // console.log(payload)
            let response = await axios({
                        url: '/parcel/evropat/get-waybill',
                        method: 'POST',
                        responseType: 'blob',
                        data: payload,
            });
            const blob = new Blob([response.data], { type: 'application/pdf' });
            const objectUrl = window.URL.createObjectURL(blob);
            window.open(objectUrl,'_blank')
            // const link = document.createElement('a');
            // link.href = objectUrl;
            // const filename = 'pratkabg_' + this.selectedWaybill + "_a4" + '.pdf'

            // Download file
            // link.setAttribute('download', filename);
            // document.body.appendChild(link);
            // link.click();
        },

        async shipmentPrintSticker() {
            const payload = {
                'shipmentBarcode': this.selectedWaybill,
                'printoutType': 'sticker'
            }
            // console.log(payload)
            let response = await axios({
                        url: '/parcel/evropat/get-waybill',
                        method: 'POST',
                        responseType: 'blob',
                        data: payload,
            });
            const blob = new Blob([response.data], { type: 'application/pdf' });
            const objectUrl = window.URL.createObjectURL(blob);
            window.open(objectUrl,'_blank')
            // const link = document.createElement('a');
            // link.href = objectUrl;
            // const filename = 'pratkabg_' + this.selectedWaybill + "_sticker" + '.pdf'

            // Download file
            // link.setAttribute('download', filename);
            // document.body.appendChild(link);
            // link.click();
        },

        shipmentByHand() {
            this.instructionsWaybill = true
        },

        // async attemptPayment() {
        //     let payload = {
        //             'parcel_invoice': this.parcel_invoice,
        //             'phone_number': this.phone_number,
        //             'shipment_type': this.shipment_type
        //         }
        //         let database_resp = await axios.post('/parcel/finished', payload)
        //         this.payment_status = database_resp.data.status
        // },

        async getInvoiceFromDB(){
            let counter = 0
            let waiting_for_db_resposne = true
            let database_resp = {}
            let payload = {
                    'parcel_invoice': this.parcel_invoice,
                    'phone_number': this.phone_number,
                    'shipment_type': this.shipment_type
                }
            
            while(waiting_for_db_resposne == true){

                database_resp = await axios.post('/parcel/finished', payload)
                console.log(database_resp)
                this.payment_status = database_resp.data.status

                if (this.payment_status == 'Incomplete' || this.payment_status == "awaitingPayment") {
                    counter += 1
                    await this.sleep(500);
                }

                else {
                    this.payment_status = database_resp.data.status
                    this.loadingData = false
                    waiting_for_db_resposne = false
                }
                if (counter >= 120) {
                    this.parcel_data = database_resp.data.parcel
                    this.loadingData = false
                    this.parcel_exists = true
                    this.payment_message_type = 'warning'
                    if(this.payment_status == 'Incomplete')
                    this.payment_message = 'Вашето плащане не е обработено. Моля, проверете дали плащането е реализирано и се свържете с нас за повече информация.'
                    this.show_payment_message = true
                    this.payment_successful = false
                    try{
                        this.tcf_data = database_resp.data.tcf_data
                    } catch (e) {
                        console.log(e)
                    }
                    return null
                }
            }
            console.log(database_resp)
            this.loadingData = false
            if ( this.payment_status == "OK" || this.payment_status == "PAID" ){
                this.parcel_data = database_resp.data.parcel
                this.parcel_exists = true
                // this.shipment_type = database_resp.data.parcel.delivery_type
                this.payment_message_type = 'success'
                this.payment_message = 'Плащането е успешно! Разпечатайте товарителницата, за да завършите изпращането!'
                this.show_payment_message = true
                this.payment_successful = true

                if(this.shipment_type==1) {
                    console.log(database_resp.data.parcel)
                    let payment_resp = await axios.post('/parcel/direx/checkout', database_resp.data.parcel)
                    console.log(payment_resp)
                }
                
            } else if (this.payment_status == 'Incomplete') {
                this.parcel_data = database_resp.data.parcel
                this.parcel_exists = true
                // this.shipment_type = database_resp.data.parcel.delivery_type
                this.payment_message_type = 'warning'
                this.payment_message = 'Пратката не е платена.'
                this.show_payment_message = true
                this.tcf_data = database_resp.data.tcf_data
            } else if (this.payment_status == 'DENIED') {
                this.parcel_data = database_resp.data.parcel
                this.parcel_exists = true
                // this.shipment_type = database_resp.data.parcel.delivery_type
                this.payment_message_type = 'warning'
                this.payment_message = 'Плащането е неуспешно.'
                this.show_payment_message = true
                this.tcf_data = database_resp.data.tcf_data
            } else if ( this.payment_status == 'noData') {
                this.payment_message_type = 'error'
                this.payment_message = 'Пратката е невалидна.'
                this.show_payment_message = true
            } else if ( this.payment_status == 'paymentComplete') {
                this.notCompleted = true
                // this.shipment_type = database_resp.data.parcel.delivery_type
                this.payment_message_type = 'info'
                this.payment_message = 'Пратката вече е платена и завършена.'
                this.show_payment_message = true
            }
        },

        async orderParcel() {
            let payload = {}

            let indexService = this.parcel_data.parcelDetails.deliveryType

            let indexType = this.parcel_data.parcelType

            if (indexService == 1){

                payload = {
