                    // 'destinationType': this.destinationType,
                    'parcelType': indexType,
                    'parcelWeight': this.parcel_data.parcelDetails.shipmentWeight,
                    'deliveryType': indexService,
                    'shipmentPrice': this.parcel_data.paymentAmount_BGN,
                    'shipmentDescription': this.parcel_data.parcelDetails.shipmentDescription,
                    'sender': {
                        'senderDestID': this.parcel_data.senderDetails.senderDestID,
                        'senderOfficeID': this.parcel_data.senderDetails.senderOfficeID,
                        'senderAddress': this.parcel_data.senderDetails.senderAddress,
                        'senderName': this.parcel_data.senderDetails.senderName,
                        'senderPhone': this.parcel_data.senderDetails.senderPhone,
                        'senderEmail': this.parcel_data.senderDetails.senderEmail,
                        'senderOrganization': this.parcel_data.senderDetails.senderOrganization,
                    },
                    'recipient': {
                        'recipientDestID': this.parcel_data.recipientDetails.recipientDestID,
                        'recipientOfficeID': this.parcel_data.recipientDetails.recipientOfficeID,
                        'recipientAddress': this.parcel_data.recipientDetails.recipientAddress,
                        'recipientName': this.parcel_data.recipientDetails.recipientName,
                        'recipientPhone': this.parcel_data.recipientDetails.recipientPhone,
                        'recipientEmail': this.parcel_data.recipientDetails.recipientEmail,
                        'recipientOrganization': this.parcel_data.recipientDetails.recipientOrganization,
                    },
                    'promoCode': ''
                }

            }
            else if (indexService == 2) {

                payload = {
                    'parcelInvoice': this.parcel_data.orderNumber,
                    // 'destinationType': this.destinationType,
                    'parcelType': indexType,
                    'parcelWeight': this.parcel_data.parcelDetails.shipmentWeight,
                    'deliveryType': indexService,
                    'shipmentPrice': this.parcel_data.paymentAmount_BGN,
                    'shipmentDescription': this.parcel_data.parcelDetails.shipmentDescription,
                    'sender': {
                        'senderDestID': this.parcel_data.senderDetails.senderDestID,
                        'senderOfficeID': this.parcel_data.senderDetails.senderOfficeID,
                        'senderAddress': this.parcel_data.senderDetails.senderAddress,
                        'senderName': this.parcel_data.senderDetails.senderName,
                        'senderPhone': this.parcel_data.senderDetails.senderPhone,
                        'senderEmail': this.parcel_data.senderDetails.senderEmail,
                        'senderOrganization': this.parcel_data.senderDetails.senderOrganization,
                    },
                    'recipient': {
                        'recipientDestID': this.parcel_data.recipientDetails.recipientDestID,
                        'recipientProvince': this.parcel_data.recipientDetails.recipientProvince,
                        'recipientCity': this.parcel_data.recipientDetails.recipientCity,
                        'recipientAddress': this.parcel_data.recipientDetails.recipientAddress,
                        'recipientName': this.parcel_data.recipientDetails.recipientName,
                        'recipientPhone': this.parcel_data.recipientDetails.recipientPhone,
                        'recipientEmail': this.parcel_data.recipientDetails.recipientEmail,
                        'recipientOrganization': this.parcel_data.recipientDetails.recipientOrganization,
                        'recipientAddressAddition': this.parcel_data.recipientDetails.recipientAddressAddition,
                        'recipientAddressNumber': this.parcel_data.recipientDetails.recipientAddressNumber,
                        'recipientBuildingNumber': this.parcel_data.recipientDetails.recipientBuildingNumber,
                        'recipientEntranceNumber': this.parcel_data.recipientDetails.recipientEntranceNumber,
                        'recipientFloorNumber': this.parcel_data.recipientDetails.recipientFloorNumber,
                        'recipientApartmentNumber': this.parcel_data.recipientDetails.recipientApartmentNumber,
                    },
                    'promoCode': ''
                }

            }
            else if (indexService == 3) {

                payload = {
                    'parcelInvoice': this.parcel_data.orderNumber,
                    // 'destinationType': this.destinationType,
                    'parcelType': indexType,
                    'parcelWeight': this.parcel_data.parcelDetails.shipmentWeight,
                    'deliveryType': indexService,
                    'shipmentPrice': this.parcel_data.paymentAmount_BGN,
