                    'promoCode': ''
                }
            }
            else if (indexService == 4) {

                payload = {
                    'parcelInvoice': this.parcel_data.orderNumber,
                    // 'destinationType': this.destinationType,
                    'parcelType': indexType,
                    'parcelWeight': this.parcel_data.parcelDetails.shipmentWeight,
                    'deliveryType': indexService,
                    'shipmentPrice': this.parcel_data.paymentAmount_BGN,
                    'shipmentDescription': this.parcel_data.parcelDetails.shipmentDescription,
                    'sender': {
                        'senderDestID': this.parcel_data.senderDetails.senderDestID,
                        'senderProvince': this.parcel_data.senderDetails.senderProvince,
                        'senderCity': this.parcel_data.senderDetails.senderCity,
                        'senderAddress': this.parcel_data.senderDetails.senderAddress,
                        'senderName': this.parcel_data.senderDetails.senderName,
                        'senderPhone': this.parcel_data.senderDetails.senderPhone,
                        'senderEmail': this.parcel_data.senderDetails.senderEmail,
                        'senderOrganization': this.parcel_data.senderDetails.senderOrganization,
                        'senderAddressAddition': this.parcel_data.senderDetails.senderAddressAddition,
                        'senderAddressNumber': this.parcel_data.senderDetails.senderAddressNumber,
                        'senderBuildingNumber': this.parcel_data.senderDetails.senderBuildingNumber,
                        'senderEntranceNumber': this.parcel_data.senderDetails.senderEntranceNumber,
                        'senderFloorNumber': this.parcel_data.senderDetails.senderFloorNumber,
                        'senderApartmentNumber': this.parcel_data.senderDetails.senderApartmentNumber,
                    },
                    'recipient': {
                        'recipientDestID': this.parcel_data.recipientDetails.recipientDestID,
                        'recipientProvince': this.parcel_data.recipientDetails.recipientProvince,
                        'recipientCity': this.parcel_data.recipientDetails.recipientCity,
                        'recipientAddress': this.parcel_data.recipientDetails.recipientAddress,
                        'recipientName': this.parcel_data.recipientDetails.recipientName,
                        'recipientPhone': this.parcel_data.recipientDetails.recipientPhone,
                        'recipientEmail': this.parcel_data.recipientDetails.recipientEmail,
                        'recipientOrganization': this.parcel_data.recipientDetails.recipientOrganization,
                        'recipientAddressAddition': this.parcel_data.recipientDetails.recipientAddressAddition,
                        'recipientAddressNumber': this.parcel_data.recipientDetails.recipientAddressNumber,
                        'recipientBuildingNumber': this.parcel_data.recipientDetails.recipientBuildingNumber,
                        'recipientEntranceNumber': this.parcel_data.recipientDetails.recipientEntranceNumber,
                        'recipientFloorNumber': this.parcel_data.recipientDetails.recipientFloorNumber,
                        'recipientApartmentNumber': this.parcel_data.recipientDetails.recipientApartmentNumber,
                    },
                    'promoCode': ''
                }
            }
            console.log(payload)
            let request = await axios.post('/parcel/evropat/checkout', payload)
            console.log('Response', request)
            if (request.data.status == 'evropatError') {
                this.checkoutPage = false
                this.editDetailsPage = true
                this.$vuetify.goTo('#checkout')
            } else if (request.data.status == 'paymentComplete')  {
                this.databaseWaybills = request.data.payload.response
                this.selectedWaybill = this.databaseWaybills[0]
                this.database_data = request.data.database
                this.checkoutPage = false
                this.waybillPage = true
                this.$vuetify.goTo('#checkout')
            }

        },

        async payParcel() {

        },

        async amendParcelData() {
            if(this.shipment_type==1) {
                    console.log(this.parcel_data)
                    let payment_resp = await axios.post('/parcel/direx/checkout', this.parcel_data)
                    console.log(payment_resp)
                }
            else{
                console.log('evropat')
            }
        },

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        },

        async isPaymentDone() {
            if (this.awaitingPaymentConfirmation === true) {
                await this.sleep(5000);
                this.checkPaymentStatus();
            }
        },

        confirmLeave() {
            return window.confirm('Do you really want to leave? you have unsaved changes!')
        },

        confirmStayInDirtyForm() {
            return !this.show_courier_message && !this.confirmLeave()
        },

        beforeWindowUnload(e) {
            if (this.confirmStayInDirtyForm()) {
                // Cancel the event
                e.preventDefault()
                // Chrome requires returnValue to be set
                e.returnValue = ''
            }   
        },
    },

    beforeRouteLeave (to, from, next) {
        // If the form is dirty and the user did not confirm leave,
        // prevent losing unsaved changes by canceling navigation
        if (this.confirmStayInDirtyForm()){
            next(false)
            window.location = to.path
        } else {
            // Navigate to next view
            next()
        }
    },

    created() {
        window.addEventListener('beforeunload', this.beforeWindowUnload)
    },

    mounted() {
        this.getInvoiceFromDB()

        var message = "Не сте повикали куриер! Ако излезнете сега, пратката Ви няма да бъде взета!";
        window.onbeforeunload = function() {
            if((this.addressOfficeService || this.addressService) && this.show_courier_message) {
                return message;
            }
        };
    },

    beforeDestroy() {
        window.removeEventListener('beforeunload', this.beforeWindowUnload)
    },
    
};
</script>

